/* eslint-disable comma-dangle */
/* eslint-disable arrow-parens */

export default class AppView {
    constructor(data) {
        this.data = data.items;
    }

    render() {
        const viewport = document.createElement('meta');
        viewport.name = 'viewport';
        viewport.content = 'width=device-width, initial-scale=1';

        document.head.appendChild(viewport);

        const fontAwesome = document.createElement('link');
        fontAwesome.rel = 'stylesheet';
        fontAwesome.href = 'https://use.fontawesome.com/releases/v5.8.2/css/all.css';
        fontAwesome.integrity = 'sha384-oS3vJWv+0UjzBfQzYUhtDYW+Pj2yciDJxpsK1OYPAYjqT085Qq/1cq5FLXAZQ7Ay';
        fontAwesome.crossOrigin = 'anonymous';
        document.head.appendChild(fontAwesome);

        function getDataTime(puplishedData) {
            const data = new Date(puplishedData);
            return data.toLocaleDateString();
        }

        const slider = document.querySelector('.slider');

        slider.innerHTML += this.data
            .map(
                (item) => `<div class='card'> <img src='${item.snippet.thumbnails.medium.url}'>
                <div><a href='https://www.youtube.com/watch?v=${item.id}' target="_blank">${item.snippet.title}</a></div>
                <h3><i class="fas fa-male"></i>${item.snippet.channelTitle}</h3>
                <p><i class="far fa-calendar-alt"></i>${getDataTime(item.snippet.publishedAt)}</p>
                <p> <i class="fas fa-eye"></i>${item.statistics.viewCount}</p>
                <p>${item.snippet.description}</p>
                </div>`
            )
            .join(' ');

        let isDown = false;
        let startX;
        let scroll;

        function sliderDown(e) {
            isDown = true;
            startX = e.pageX - slider.offsetLeft;
            scroll = slider.scrollLeft;
            slider.classList.add('active');
        }
        slider.addEventListener('mousedown', sliderDown, false);
        slider.addEventListener('touchstart', sliderDown, false);

        function sliderUp() {
            isDown = false;
            slider.classList.remove('active');
        }
        slider.addEventListener('mouseup', sliderUp, false);
        slider.addEventListener('touchend', sliderUp, false);

        function sliderMove(e) {
            if (!isDown) return;
            e.preventDefault();
            const nextPoint = e.pageX - slider.offsetLeft;
            const difference = (nextPoint - startX) * 2;
            slider.scrollLeft = scroll - difference;
        }

        slider.addEventListener('mousemove', sliderMove, false);
        slider.addEventListener('touchmove', sliderMove, false);
    }
}
